.footer {
  background-color: #2d3436;
  color: white;
  padding: 4rem 0 0;
  margin-top: 4rem;
}

.container {
  display: grid;
  grid-template-columns: 1.5fr 1fr 1fr 1fr;
  gap: 2rem;
  padding-bottom: 3rem;
}

.logo {
  font-family: var(--font-nunito);
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.highlight {
  color: var(--primary);
}

.text {
  color: #b2bec3;
  line-height: 1.6;
  margin-bottom: 1rem;
}

.heading {
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
  color: white;
}

.link {
  display: block;
  color: #b2bec3;
  margin-bottom: 0.75rem;
  transition: color var(--transition-fast);
  font-size: 0.95rem;
}

.link:hover {
  color: var(--primary);
}

.socials {
  display: flex;
  gap: 1rem;
}

.socialIcon {
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background var(--transition-fast);
}

.socialIcon:hover {
  background-color: var(--primary);
}

.bottom {
  background-color: #222526;
  padding: 1.5rem 0;
}

@media (max-width: 768px) {
  .container {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .socials {
    justify-content: center;
  }
}
